<template>
  <div>
    <h2>Login</h2>
    <div class="row">
      <div class="card mx-auto">
        <div class="card-header text-white ">
          <h4 class="text-center fw-bold fs-1 mt-3">Login</h4>
        </div>
        <div class="card-body">
          <form @submit.prevent="loginUser">
            <div class="form-group">
              <label for="username" class="fw-strong fs-4 text-white">Username</label>
              <input
                type="text"
                id="username"
                name="username"
                placeholder="Username"
                v-model="username"
                class="form-control mt-1"
                required
              />
            </div>
            <div class="form-group">
              <label for="password" class="fw-strong fs-4 text-white mt-4">Password</label>
              <input
                type="password"
                id="password"
                name="password"
                placeholder="Password"
                v-model="password"
                class="form-control mt-1"
                required
              />
            </div>
            <input type="submit" class="btn btn-dark mt-3" value="Login" /><br>
            <span class="text-white mt-3">Dont have any account ?</span>
            <router-link to="/register-student" class="card-link ms-2 btn btn-primary">Register</router-link>
          </form>
        </div>
      </div>
    </div>






  </div>
</template>

<script>
import { mapActions } from "vuex";


export default {
  name: "LoginUser",
  data() {
    return {
      username: "",
      password: "",
      name:""
    };
  },
  methods: {
    ...mapActions(["login"]),
    loginUser() {
      let user = {
        username: this.username,
        password: this.password,
      };
      this.login(user)
        .then((res) => {
          if (res.data.success) {
            if(res.data.user.role === 'student'){
              this.$router.push("/student-dashboard");                
            }
            else
            this.$router.push("/home");
          }
          else{
            this.$toasted.show(`${res.data.error}`);

          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },

};
</script>

<style scoped>

.card{
  width:50%;
background:linear-gradient(180deg, rgba(0,0,0,1) 0%, rgba(23,74,84,1) 100%)
}





